import { fetchMovies } from './api/fetchMovies';
import { renderList } from './renderListMarkup';

// Wyszukuje filmy po wpisaniu w wyszukiwarce

const searchMovies = () => {
  const searchInput = document.getElementById('search');
  const searchValue = searchInput.value.trim();

  fetchMovies(searchValue)
    .then(movies => {
      const markup = renderList(movies);
      document.getElementById('main').innerHTML = markup;

      // wywoanie funkcji `openModal` z odpowiednimi argumentami po utworzeniu elementw z wynikami wyszukiwania.
      const movieCards = document.querySelectorAll('.movie-card');
      movieCards.forEach(movieCard => {
        movieCard.addEventListener('click', () => {
          // Pobierz dane filmu z atrybutw `data-*` kliknitego elementu `.movie-card`.
          console.log(movieCard);
        });
      });
    })
    .catch(error => {
      console.error('Error:', error);
      // Tutaj moesz wywietli informacj o bdzie w interfejsie uytkownika
    });
};


export { searchMovies };
